import {  
    mapMutations  
} from 'vuex';
export default {
	data() {
		return {
			
		};
	},
	methods:{
		...mapMutations(['logout']),
		/**
		 * 统一跳转接口,拦截未登录路由
		 * navigator标签现在默认没有转场动画，所以用view
		 */
		toPage(url){
			// if(!this.hasLogin){
			// 	url = '/pages/public/login';
			// }
			uni.navigateTo({  
				url
			})  
		}, 
		navTo(url){
			this.$api.msg(`跳转到${url}`);
		},
		//退出登录
		toLogout(){
			uni.showModal({
				title: '确定要退出登录么？',
				success: (e)=>{
					if(e.confirm){
						this.logout();
						setTimeout(()=>{
							uni.navigateBack();
						}, 200)
					}
				}
			})
		},
		//switch
		switchChange(e){
			let statusTip = e.detai ? '打开': '关闭';
			this.$api.msg(`${statusTip}消息推送`);
		},

	}
}
